.header {
  width: 100%;
  margin-bottom: 20px;
  padding: 8px;

  border-bottom: 1px solid #dfdfdf;

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 16px;
    min-height: 50px;

    @media (max-width: 420px) {
      justify-content: center;
    }
  }
}